.forwardMov-enter-active {
  -webkit-animation: forwardMov-in 0.4s;
          animation: forwardMov-in 0.4s;
}
.forwardMov-leave-active {
  -webkit-animation: forwardMov-out 0.4s;
          animation: forwardMov-out 0.4s;
}
@-webkit-keyframes forwardMov-in {
0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    opacity: 0;
}
100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity: 1;
}
}
@keyframes forwardMov-in {
0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    opacity: 0;
}
100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity: 1;
}
}
@-webkit-keyframes forwardMov-out {
0% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity: 1;
}
100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    opacity: 0;
}
}
@keyframes forwardMov-out {
0% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity: 1;
}
100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    opacity: 0;
}
}
.backMov-enter-active {
  -webkit-animation: backMov-in 0.4s;
          animation: backMov-in 0.4s;
}
.backMov-leave-active {
  -webkit-animation: backMov-out 0.4s;
          animation: backMov-out 0.4s;
}
@-webkit-keyframes backMov-in {
0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    opacity: 0;
}
100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity: 1;
}
}
@keyframes backMov-in {
0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    opacity: 0;
}
100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity: 1;
}
}
@-webkit-keyframes backMov-out {
0% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity: 1;
}
100% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    opacity: 0;
}
}
@keyframes backMov-out {
0% {
